.loading {
    position: relative;
    overflow: hidden;
    opacity: 0.3;
}

.loading:after {
    content: '';
    position: absolute;
    left: 0;
    top: 50%;
    bottom: 0;
    height: 300px;
    width: 100%;
    margin-top: -150px;
    transform: rotate(-120deg);
    background: linear-gradient(rgba(255, 255, 255, 0) 0%, white 50%, rgba(255, 255, 255, 0) 100%);
    animation: shiny 1.5s infinite;
}

@keyframes shiny {
    0% {
        left: -100%;
    }

    100% {
        left: 100%;
    }
}

.loading-old {
    position: relative;

    &::before {
        content: '';
        position: absolute;
        left: 0;
        right: 0;
        top: 0;
        bottom: 0;
        background-color: white;
        opacity: 0.5;
    }

    &::after {
        content: '';
        position: absolute;
        top: 50%;
        left: 50%;
        margin-top: -10px;
        margin-left: -10px;
        width: 16px;
        height: 16px;
        border-radius: 20px;
        border: 4px solid lightgray;
        border-top-color: grey;
        animation: loading-old 1s linear infinite;
    }
}

@keyframes loading-old {
    0% {
        transform: rotate(0deg);
    }

    100% {
        transform: rotate(360deg);
    }
}