.snipcart {
    font-family: Open Sans, sans-serif;
    font-size: 14px;
    font-weight: 300;
    color: #2e3338;
    color: var(--color-default, #2e3338)
}

.snipcart * {
    margin: 0;
    padding: 0;
    border: 0;
    font: inherit;
    color: inherit;
    background: 0 0;
    display: block;
    line-height: 1;
    list-style: none;
    quotes: none;
    border-collapse: collapse;
    border-spacing: 0;
    box-sizing: border-box;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale
}

.snipcart__font--secondary,
.snipcart__font--subtitle,
.snipcart__font--subtitle-small {
    font-family: Montserrat, sans-serif
}

.snipcart__font--small {
    font-size: 10px
}

.snipcart__font--subtitle-small,
.snipcart__font--tiny {
    font-size: 12px
}

.snipcart__font--std {
    font-size: 14px;
    line-height: 20px
}

.snipcart__font--large {
    font-size: 16px
}

.snipcart__box--badge,
.snipcart__font--subtitle,
.snipcart__font--xlarge {
    font-size: 18px
}

.snipcart__font--slim {
    font-weight: 300
}

.snipcart__font--regular {
    font-weight: 400
}

.snipcart__font--bold {
    font-weight: 500;
    line-height: 20px
}

